      <span>{{ link.name }}</span>
    </a>
  </nav>
</template>

<script>
export default {
  data () {
    return {
      active: '',
      linkData: [
        {
          href: 'home',
          name: 'Home'
        },
        {
          href: 'video',
          name: '形象影片'
        },
        {
          href: 'intro',
          name: 'LatteGo介紹'
        },
        {
          href: 'feature',
          name: '重點特色'
        },
        {
          href: 'detail',
          name: '細部介紹'
        },
        {
          href: 'people',
          name: '口碑推薦'
        },
        {
          href: 'shop',
          name: '販售店點'
        },
        {
          href: 'specifications',
          name: '技術規格'
        },
        {
          href: 'product',
          name: '機型比較'
        }
      ]
    }
