<style lang="scss">
  @import "../assets/scss/root";
  .site-nav {
    position: fixed;
    right: 2rem;
    top: 50%;
    transform: translate3d(0,-50%,0);
    z-index: 100;
    @include susy-breakpoint($s-320, $c8) {
      display: none;
    }
    @include susy-breakpoint($s-768, $c12) {
      display: none;
    }
    &__btn {
      display: block;
      text-align: right;
      position: relative;
      padding-right: 0.6rem;
      height: 2rem;
      line-height: 2rem;
      overflow: hidden;
      &:not(:last-child) {
        margin-bottom: 1rem;
      }
      &::before {
        content: "";
        width: 0.1rem;
        height: 2rem;
        background-color: #818282;
        position: absolute;
        right: 0;
        top: 0;
      }
      &::after {
        content: "";
        width: 0.3rem;
        height: 2rem;
        background-color: var(--color-main);
        position: absolute;
        right: -0.1rem;
        top: 0;
        border-radius: 1rem;
        opacity: 0;
        transition: all 300ms ease;
      }
      span {
        display: block;
        font-size: var(--font-size-18);
        color: var(--color-main);
        transition: all 300ms ease;
        opacity: 0;
        position: relative;
        transform: translateX(100%);
      }
      &:hover {
        span {
          opacity: 1;
          transform: translateX(0);
        }
        &::after {
          opacity: 1;
        }
      }
      &.active {
        span {
          opacity: 1;
          transform: translateX(0);
        }
        &::after {
          opacity: 1;
        }
      }
    }
  }
</style>