<template>
  <Nav />
  <Home class="section-block" />
  <Video class="section-block" />
  <Intro class="section-block" />
  <Feature class="section-block" />
  <Detail class="section-block" />
  <People class="section-block" />
  <PeopleMobile />
  <Shop class="section-block" />
  <Specifications class="section-block" />
  <Products class="section-block" />
</template>

<script>
import Nav from './components/Nav'
import Home from './components/Home'
import Video from './components/Video'
import Intro from './components/Intro'
import Feature from './components/Feature'
import Detail from './components/Detail'
import People from './components/People'
import PeopleMobile from './components/PeopleMobile'
import Shop from './components/Shop'
import Specifications from './components/Specifications'
import Products from './components/Products'

export default {
  name: 'App',
  components: {
    Nav,
    Home,
    Video,
    Intro,
    Feature,
    Detail,
    People,
    PeopleMobile,
    Shop,
    Specifications,
    Products
  },
  data () {
    return {
      sectionName: ''
    }
  },
  created () {
    this.$store.commit('setTarget', 'home')
    window.addEventListener('scroll', this.handleScroll);
  },
  mounted () {
    this.activeNav()
  },
  methods: {
    handleScroll () {
      const els = document.querySelectorAll('.section-block')
      els.forEach((el) => {
        const elTop = el.getBoundingClientRect().top
        const elBottom = el.getBoundingClientRect().bottom
        if (elTop >= 0 || elBottom <= 0) {
          this.sectionName = el.id
        } if (elTop <= 0 && elBottom >= 0) {
          this.$store.commit('setTarget', el.id)
          this.activeNav()
        } 
      })
    },
    activeNav () {
      this.sectionName = this.$store.state.target
      console.log(this.sectionName)
      // if(this.sectionName === 'video') {
      //   document.querySelector('#video-intro').play()
      // } else {
      //   document.querySelector('#video-intro').pause()
      // }
      document.querySelectorAll('.site-nav__btn').forEach( btn => {
        if( btn.rel === this.sectionName ) {
          btn.classList.add('active')
        } else {
          btn.classList.remove('active')
        }
      })
    }
  }
}
</script>

