            {
              name: "可回收包裝材質",
              value: [">95 %"],
            }
          ]
        }
      ],
      realData: []
    }
  },
  mounted () {
    this.pushData()
  },
  methods: {
    pushData () {
      this.realData = []
      this.specificationsData.forEach((item, index) => {
        if(index <= this.showLength) {
          this.realData.push(item)
        }
      })
    },
    onToggleDisplay () {
      const length = this.specificationsData.length
      
      if( this.showLength < length) {
        this.showLength = length
        this.showText = '隱藏部分技術規格'
      } else {
        this.showLength = 2
        this.showText = '顯示全部技術規格'
      }
      this.pushData()
    }
  }
}
