<style lang="scss">
@import "../assets/scss/root";
.specifications {
  background-color: #543f2e;
  padding: var(--space-80) 0;
  $self: &;
  &__title-h3 {
    flex-basis: 20rem;
    font-size: var(--font-size-30);
    color: #fff;
    font-weight: 500;
    @include susy-breakpoint($s-320, $c8) {
      font-size: var(--font-size-24);
      flex-basis: 8rem;
    }
    @include susy-breakpoint($s-768, $c12) {
      font-size: var(--font-size-18);
    }
    @include susy-breakpoint($s-960, $c12) {
      font-size: var(--font-size-20);
    }
  }
  &__content {
    flex-grow: 1;
  }
  &__title-h4 {
    flex-basis: 24rem;
    font-size: var(--font-size-20);
    color: var(--color-main);
    font-weight: 500;
    line-height: 1.6;
    @include susy-breakpoint($s-320, $c8) {
      font-size: var(--font-size-14);
    }
    @include susy-breakpoint($s-768, $c12) {
      font-size: var(--font-size-14);
    }
    @include susy-breakpoint($s-960, $c12) {
      font-size: var(--font-size-16);
    }
  }
  &__item {
    align-items: center;
    padding-bottom: var(--space-40);
    padding-top: var(--space-40);
    display: flex;
    @include susy-breakpoint($s-320, $c8) {
      padding-bottom: var(--space-20);
      padding-top: var(--space-20);
      &:nth-child(1n+3) {
        flex-wrap: wrap;
        #{$self}__title-h3 {
          flex-basis: 100%;
        }
      }
    }
    &.active {
      display: flex;
    }
    &:not(:last-child) {
      border-bottom: solid 0.1rem var(--color-main);
    }
  }
  &__content-item {
    display: flex;
    align-items: center;
    padding-bottom: var(--space-8);
    padding-top: var(--space-8);
    @include susy-breakpoint($s-320, $c8) {
      display: block;
    }
  }
  &__value {
    p {
      font-size: var(--font-size-20);
      color: #fff;
      padding-bottom: var(--space-8);
      padding-top: var(--space-8);
      @include susy-breakpoint($s-320, $c8) {
        font-size: var(--font-size-14);
      }
      @include susy-breakpoint($s-768, $c12) {
        font-size: var(--font-size-14);
      }
      @include susy-breakpoint($s-960, $c12) {
        font-size: var(--font-size-16);
      }
    }
  }
  &__btn {
    text-align: center;
    border: 0;
    padding: 0;
    background-color: transparent;
    display: block;
    margin: 0 auto;
    span {
      display: inline-block;
      font-size: var(--font-size-20);
      border: solid 0.3rem #fff;
      border-radius: 6rem;
      padding: var(--space-10) var(--space-40);
      color: #fff;
      @include susy-breakpoint($s-320, $c8) {
