      @include susy-breakpoint($s-960, $c12) {
        font-size: var(--font-size-18);
      }
    }
  }
}
.slide-leave-active,
.slide-enter-active {
  transition: 1s;
}
.slide-enter {
  opacity: 1;
}
.slide-enter-from {
  opacity: 0;
}
.slide-leave-from {
  opacity: 1;
}
.slide-leave-to {
  opacity: 0;
}
</style>
