<template>
  <div class="people" id="people">
    <div class="container">
      <h2 class="section-title" data-aos="fade-up">
        <img src="../assets/images/lattego-text-w.svg" alt="LatteGo">
        <span>一鍵按出輕享受</span>
      </h2>
      <h3 class="people__h3" data-aos="fade-up">享受雲朵拿鐵  隨時品味細節發酵的每個瞬間</h3>
      <div class="people__wrap" data-aos="fade-up">
        <div class="people__item people-1" @mouseover="onItemHover($event)" @mouseout="onItemOut($event)" @click="onClickLink(link[0])"></div>
        <div class="people__item people-2" @mouseover="onItemHover($event)" @mouseout="onItemOut($event)" @click="onClickLink(link[1])"></div>
      </div>
      <div class="people__name-wrap">
        <span class="people__name">#精神科觀察日記</span>
        <span class="people__name">#法呢</span>
