  </div>
</template>

<script>
export default {
  data() {
    return {
      link: [
        'https://www.facebook.com/permalink.php?story_fbid=3598244173543557&id=523219677712704',
        'https://www.instagram.com/p/CDwDkD8DbQD/?igshid=1t4iwkgio0pks'
      ]
    }
  },
  methods: {
    onItemHover (event) {
      event.target.classList.add('expand')
      event.target.children[0].classList.add('active')
    },
    onItemOut (event) {
      event.target.classList.remove('expand')
      event.target.children[0].classList.remove('active')
    },
    onClickLink (url) {
      window.open(url, '_blank').focus()
    }
  }
}
</script>

