<template>
  <div class="video" id="video">
    <button :class="[isMuted ? 'video__btn-sound':'video__btn-sound off']" @click="toggleSound()"></button>
    <video id="video-intro" src="../assets/video/video.mp4" loop muted playsinline autoplay></video>
  </div>
</template>

<script>
export default {
  data () {
    return {
      isMuted: true
    }
  },
  methods: {
    toggleSound () {
      if(this.isMuted) {
        document.querySelector('#video-intro').muted = false
        this.isMuted = false
      } else {
        document.querySelector('#video-intro').muted = true
        this.isMuted = true
      }
    }
  }
}
</script>

<style lang="scss">
  @import "../assets/scss/root";
  .video {
    background-color: #938681;
    position: relative;
    padding-bottom: 56.25%;
    video {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      z-index: 50;
    }
    &__btn-sound {
      background: transparent url('../assets/images/icon-on.svg') no-repeat 0 0;
      background-size: cover;
      width: 4rem;
      height: 4rem;
      position: absolute;
      z-index: 100;
      bottom: 12%;
      right: 4rem;
      border: 0;
      @include susy-breakpoint($s-320, $c8) {
        width: 2.4rem;
        height: 2.4rem;
        bottom: 10%;
        right: 1.6rem;
        background-size: cover;
      }
      @include susy-breakpoint($s-768, $c12) {
        width: 3.6rem;
        height: 3.6rem;
        bottom: 3rem;
        right: 3rem;
        bottom: 10%;
        background-size: cover;
      }
      &.off {
        background-image: url('../assets/images/icon-off.svg');
      }
    }
  }
</style>